.container {
  display: flex;
  flex-direction: column;
}

.hidden {
  display: none;
}

.main {
  display: flex;
  flex-direction: column;
}

.main h1 {
  display: flex;
  flex-direction: column;
  font-size: 2em;
  border-top: 2px solid black;
  border-bottom: 2px solid black;
  text-align: center;
  width: 80%;
  margin: 10px auto;
  padding: 0.3em;
  text-transform: uppercase;
}

h2 {
  display: flex;
  flex-direction: column;
  font-size: 1.5em;
  text-align: center;
  width: 60%;
  margin: 10px auto;
  padding: 0.3em;
}

.competences {
  width: 80%;
  margin: 10px auto;
}

.logoCompetences {
  flex-direction: row;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

svg.icon {
  width: 100px;
  height: 100px;
  align-content: space-between;
  height: 100px;
  margin: 20px 0px 15px 0px;
  padding: 0.5em;
}
